.Dashboard {
	background-color: #F5F7FB;
    padding: 32px 24px;
	
	.title {
		font-family: Usual;
		font-weight: 700;
		font-size: 2rem;
		align-items: flex-start;
		color: #272E4C;
	}

	.sub-title {
		font-family: Usual;
		font-weight: 600;
		font-size: 1.25rem;
		color: #9EA3AC;
		margin-bottom: 16px;
	}

	.content {
		min-height: 200px;
		border-radius: 12px;
		margin-bottom: 16px;
		overflow: hidden;
		box-shadow: 0px 3px 6px #272E4C19;
	}

	.loading-indicator {
		color: green[500];
		justify-content: 'center';
        margin: auto;
        z-index: -1;
	}

	.card-header {
		height: 124px;
		background-color: #FAFCFE;
		padding: 16px;
	}

	.card-graph-header {
		padding: 16px 0px;
		margin: 0px 16px;
	}

	.card-content {
		min-height: 200px;
		padding: 0px 16px;
	}

	.card-footer {
		padding: 16px;
	}
	
	.dashboard-temp-graph-img-report {
		height: 240px;
		max-width: 100%;
		margin: 16px 0px;
	}

	.dashboard-temp-graph-img {
		width: 100%;
		margin: 16px 0px;
	}

	.dashboard-graph-title {
		font-family: Usual;
		font-weight: 600;
		font-size: 1.5rem;
		color: #151C1B;
	}

	.dashboard-count {
		font-size: 2.25rem;
	}

	.dashboard-count-description {
		font-family: Usual;
		font-weight: 600;
		font-size: 0.875rem;
		color: #151C1B;
	}

	.dashboard-table-hg-even-row{
		background-color: #FFF1F1;
		padding: 4px 0px;
	}

	.dashboard-table-even-row{
		background-color: #FFFFFF;
		padding: 4px 0px;
	}

	.dashboard-table-hg-odd-row{
		background-color: #F7EAEC;
		padding: 4px 0px;
	}

	.dashboard-table-odd-row{
		background-color: #FAFCFE;
		padding: 4px 0px;
	}

	.dashboard-table-header-cell {
		font-family: Usual;
		font-weight: 700;
		font-size: 0.875rem;
		color: #3C394A;
		min-width: 150px;
	}

	.dashboard-table-data-cell {
		font-family: Usual;
		font-size: 0.875rem;
		color: #151C1B;
		cursor: default;
	}

	.dashboard-table-data-cell-sm {
		font-family: Usual;
		font-size: 0.75rem;
		color: #151C1B;
		cursor: default;
	}
	.dashboard-table-data-cell-sm:hover {
		cursor: pointer;
	}

	.dashboard-table-data-cell-link {
		font-family: Usual;
		font-size: 0.75rem;
		color: #151C1B;
		text-decoration: underline;
		cursor: pointer;
	}

	.status-circle {
		font-size: 8px;
		color: #9EA3AC;
	}

	.status-circle-open {
		color: rgb(65, 182, 127);
	}

	.status-circle-pending {
		color: rgb(248, 179, 68);
	}

	.status-circle-resolved {
		color: rgb(127, 58, 171);
	}
	 
	.status-circle-closed {
		color: rgb(41, 177, 195);
	}

	.urgency-indicator {
		font-family: Usual;
		font-size: 0.6rem;
		font-weight: 600;
		width: 16px;
		height: 16px;
		border-radius: 4px;
	}

	.urgency-1 {
		color: white;
		background: rgb(250, 86, 86);
	}
	.urgency-2 {
		color: white;
		background-color: rgb(248, 179, 68);
	}
	.urgency-3 {
		color: white;
		background-color: rgb(65, 182, 127);
	}

	.dashboard-list-item-text {
		font-family: Usual;
		font-size: 0.875rem;
		color: #151C1B;
	}

	.dashboard-list-item-text2 {
		font-family: Usual;
		font-size: 0.875rem;
		color: rgba(21,28,27,0.54);
	}

	.dashboard-list-item-link {
		font-family: Usual;
		font-weight: 700;
		font-size: 0.875rem;
		color: #0D5E40;
		text-decoration: none;
	}

	.dashboard-list-item-link2 {
		font-family: Usual;
		font-weight: 700;
		font-size: 0.875rem;
		color: rgba(13,94,64,0.54);
		text-decoration: none;
	}

	.dashboard-view-more-link {
		font-family: Usual;
		font-weight: 700;
		font-size: 0.875rem;
		color: #0D5E40;
		text-decoration: none;
	}

	.dashboard-list-item-link-disabled {
		font-family: Usual;
		font-weight: 700;
		font-size: 0.875rem;
		color: #0d5e4080;
		text-decoration: none;
		
	}

	.dashboard-table-data-cell-disabled {
		font-family: Usual;
		font-size: 0.875rem;
		color: #151c1b80;
	}

	.dashboard-claim-link {
		color:#3AB77D;
		font-weight:bold; 
		font-size:14px;
		line-height:17px;
	}
}
